      title.textContent = attributes.title;
      ArrowRightDirective.titleIdCounter++;
      title.setAttribute("id", `arrow-right-${ArrowRightDirective.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `arrow-right-${ArrowRightDirective.titleIdCounter}`);
